<template>
  <div>
    <!-- 店铺信息头 -->
    <div class="shopHeader">
      <div class="shopName">{{ shopDetail.sname }}</div>
      <div class="shopInfo">
        <div>订餐电话:{{ shopDetail.tel }}</div>
        <div>店铺位置:{{ shopDetail.location }}</div>
      </div>
    </div>
    <!-- 店铺菜品区域 -->
    <div class="foodArea">
      <el-empty
        :image-size="200"
        description="暂无菜品"
        v-show="foodList.length == 0"
      ></el-empty>
      <!-- 遍历菜品列表的菜品 -->
      <div
        class="foodItem"
        v-for="food in foodList"
        :key="food.did"
        @click="goDetail(food.did)"
      >
        <div class="foodPic">
          <img :src="food.dimage" style="width: 100%" />
        </div>
        <div class="foodInfo">
          <div class="foodTitle">{{ food.dname }}</div>
          <div><span class="money">￥</span>:{{ food.dprice }}.00</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { shopAPI, addShopCartAPI } from "@/api/index";
export default {
  name: "shop",
  // =============================================以下是data=============================================
  data() {
    return {
      shopID: "",
      shopDetail: {},
      foodList: [],
    };
  },
  // =============================================以下是Method=============================================
  methods: {
    // 跳转至菜品详情
    goDetail(foodID) {
      this.$router.push({
        name: "Food",
        query: {
          foodID: foodID,
        },
      });
    },
    // 初始化店铺
    async initShop() {
      const { data: res } = await shopAPI(this.shopID);
      if (res.code !== 0) this.$message.error("Sorry Something Wrong!");
      this.shopDetail = res.data.shop;
      this.foodList = res.data.dishes;
    },
  },
  // =============================================以下是Created=============================================
  created() {
    this.shopID = this.$route.query.shopID;
    this.initShop();
  },
  // =============================================以下是Mounted=============================================
  mounted() {},
  // =============================================以下是watch=============================================
  watch: {
    $route() {
      this.shopID = this.$route.query.shopID;
      this.initShop();
    },
  },
};
</script>

<style scoped>
/* 店铺头 */
.shopHeader {
  width: 90%;
  height: 150px;
  margin: 10px auto;
  border: rgb(154, 152, 152) solid 3px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  position: relative;
}
.shopName {
  width: 500px;
  height: 80px;
  font-size: 50px;
  font-weight: 700;
  font-family: "楷体";
  text-align: center;
  line-height: 80px;
  color: rgb(104, 101, 101);
  position: absolute;
  top: 30px;
  left: 320px;
}
.shopInfo {
  width: 100%;
  height: 30px;
  font-size: 10px;
  font-family: "幼圆";
  font-weight: 400;
  color: rgb(149, 152, 154);
  background-color: rgb(183, 213, 236);
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  position: absolute;
  bottom: 0px;
  left: 0px;
}
/* 店铺体 */
.foodArea {
  width: 100%;
  background-color: rgb(211, 220, 230);
  border: rgb(184, 185, 185) solid 4px;
  border-radius: 15px;
  margin-top: 30px;
  overflow: auto;
}
.foodItem {
  width: 170px;
  height: 216px;
  background-color: antiquewhite;
  margin: 20px 20px;
  float: left;
}
.foodItem:hover {
  cursor: pointer;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.foodPic {
  height: 170px;
  width: 100%;
  background-color: rgb(186, 212, 220);
}
.foodTitle {
  padding-left: 5px;
  font-size: 12px;
}
.money {
  color: orange;
  font-weight: 600;
  font-size: 20px;
}
</style>
